@import url(https://fonts.googleapis.com/css2?family=Raleway:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap);
html,
body {
  margin: 0em;
  padding: 0em;
}

html {
  background-color: #090f1a;
  background-image: linear-gradient(
      to bottom,
      rgba(0, 0, 0, 0),
      rgba(0, 0, 0, 0)
    ),
    url(data:image/png;base64,);
  background-repeat: repeat;
  background-size: 650px auto;
  width: 100vw;
  min-height: 100vh;
  overflow: hidden;
  box-shadow: inset 0px 0px 50px 50px #090f1a;
}

body {
  width: 100%;
  min-height: 100vh;
  overflow: hidden;
}

#root {
  width: 100%;
  min-height: 100%;
  overflow: hidden;
}

.App {
  width: 100%;
  min-height: 100vh;
  overflow: hidden;
  padding: 5em 10em;
  box-sizing: border-box;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  -ms-box-sizing: border-box;
  -o-box-sizing: border-box;
}

.cloudBlur {
  position: fixed;
  top: -25vw;
  left: 40vh;
  width: 100vw;
  height: 100vw;
  background-image: url(/static/media/Coloured-Cloud.28b2fb05.png);
  background-size: cover;
  background-position: 0;
  z-index: -1;
}

.App h2 {
  font-family: "Raleway", sans-serif;
  font-size: 39px;
  font-weight: 500;
  line-height: 46px;
  letter-spacing: 0.1em;
  text-align: left;
  color: white;
}

.App h1 {
  font-family: "Raleway", sans-serif;
  font-size: 105px;
  font-weight: 700;
  line-height: 123px;
  letter-spacing: 0.1em;
  text-align: left;
  color: white;
}

.App p{
  font-family: "Raleway", sans-serif;
  font-size: 25px;
  font-weight: 600;
  line-height: 29px;
  letter-spacing: 0.1em;
  text-align: left;
  color: white;
}


.App p span{
    margin-left: .35em;
}

.App a {
    text-decoration: none;
    color: white;
}

.App p span:hover{
    color: #38BDF8;
}

.App p span{
  display: inline-flex;
  align-items: center;
}

@media only screen and (max-width: 700px) {
  .App {
    padding: 5em;
  }

  .App h2 {
    font-size: 35px;
    line-height: 50px;

  }
  .App h1 {
    font-size: 4em;
    line-height: 120px;

  }

  .App p {
    line-height: 50px;
    position: fixed;
    bottom: 5em;
  }
}


@media only screen and (max-width: 425px) {
  .App {
    padding: 3em;
  }

  .App h2 {
    font-size: 1.5em;
    line-height: 50px;

  }
  .App h1 {
    font-size: 3.25em;
    line-height: 2em;

  }

  .App p {
    font-size: 1.5em;
    line-height: 2em;
    position: fixed;
    bottom: 5em;
  }
}
